}

.social-media-menu:hover a {
    opacity: 0.5; 
}

.social-media-menu a:hover {
    opacity: 1;
}

.footer-caption-container {
    margin: 25px auto 0;
    width: -webkit-fit-content;
    width: -moz-fit-content;
