body {
  font-family: "Ubuntu", sans-serif;
  font-size: 18px;
  color: #010000;
  font-weight: 400;
  font-style: normal;
}

.container-fluid {
  line-height: 24px;
}

header {
  position: relative;
  display: inline-block;
  width: 100%;
  background: #ffffff;
  color: #ffffff;
  height: 60px;
  border-bottom-style: solid;
  border-bottom-width: thin;
}
header nav,
header nav ul,
header nav ul li {
  white-space: nowrap;
  overflow: hidden;
}
header nav {
  position: absolute;
  top: 50%;
  right: 15px;
  margin-top: 10px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
header nav ul li {
  display: inline-block;
  margin-right: 10px;
  padding: 10px;
}
header nav ul li.last {
  margin: 0;
}
header nav ul li a {
  font-size: 20px;
  font-family: "Ubuntu", sans-serif;
  line-height: normal;
  color: #010000;
  text-decoration: none;
  transition: 0.01s ease-in-out;
  cursor: pointer;
}
header nav ul li a:hover {
  text-decoration: none;
  color: #0d98ba;
  transition: 0.01s ease-in-out;
}

